.footer {
    display: none;
    padding-bottom: 35px;
    
    @include sm-down {
        display: block;
    }
    
    &__wrap {
        margin: 0 auto;
        text-align: center;
    }
    
    .header__wrap-phone {
        margin-right: 0;
        text-align: center;
    }
    
    .header__phone-number {
        margin-bottom: 12px;
    }
    
    .header__phone-adds {
        text-align: center;
        margin-bottom: 20px;
        font-size: 18px;
    }
    
    .header__button {
        display: inline-block;
    }
}