.adress {
    
    display: flex;
    align-items: center;
    
    &__map {
        display: block;
        width: 56px;
        height: 56px;
        margin-right: 25px;
        &-img {
            display: block;
            width: 100%;
            height: auto;
        }
    }
    
    &__info {
    
    }
    
    &__office {
        font-size: 14px;
        font-weight: 300;
        line-height: 24px;
        color: #424752;
        //margin-bottom: 11px;
    }
    
    &__adds {
        color: #424752;
        font-size: 16px;
        font-weight: 400;
        line-height: 24px;
        //margin-bottom: 12px;
    }
    
    &__time {
        color: #424752;
        font-size: 14px;
        font-style: italic;
        line-height: 24px;
    }
}