// ADD FONTS
@font-face {
    font-family: "KOMIKA AXIS";
    src: url("./../fonts/KOMIKAX/KOMIKAX.eot");
    src: url("./../fonts/KOMIKAX/KOMIKAX.eot?#iefix") format("embedded-opentype"),
        url("./../fonts/KOMIKAX/KOMIKAX.woff") format("woff"),
        url("./../fonts/KOMIKAX/KOMIKAX.woff2") format("woff2"),
        url("./../fonts/KOMIKAX/KOMIKAX.ttf") format("truetype");
    font-weight: normal;
    font-style: normal;
}

