:root {
  --header-bg-color: #FFFFFFF;
  --header-title-font-color: #666666;
  --header-undertitle-font-color: #CCCCCC;
  
  --aside-bg-color: #05212A;
  --aside-title-font-color: #FFFFFFF;
  --aside-undertitle-font-color: #6E8E97;
  --aside-item-font-color: #A3ACAE;
  --aside-item-hover-font-color: #FFFFFFF;
  --aside-button-bg-color: #2A4249;
  
  --main-bg-color: #D9E8F2;
  --main-title-font-color: #000000;
  --main-time-font-color: #CFCFCF;
  --main-button-font-color: #FFFFFF;
  --main-button-bg-color: #19BCF3;
  --main-block-bg-color: #FFFFFF;
  
  --closed-task-bg-color: #5BBD72;
  --opened-task-bg-color: #19BCF3;
  --task-bg-color: #F3F3F3;
  --task-font-color: #FFFFFF;
  --task-time-color: #AAAAAA;
}

html {
  font-size: 16px;
  box-sizing: border-box;
  -ms-overflow-style: scrollbar;
}

body {
  font-size: 100%;
  font-weight: 400;
  line-height: 1.15;
  font-family: $FontPrimary;
  color: $color_primary;
  position: relative;
  min-width: 320px;
  //overflow-x: hidden;
  -ms-overflow-style: scrollbar;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  position: relative;
  width: 100%;
  max-width: 1250px;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;

  @include xl-down {
    max-width: 1170px;
  }

  @include lg-down {
    max-width: 100%;
  }
  //
  //&--inner {
  //  max-width: 1470px;
  //  width: 100%;
  //  padding-right: 15px;
  //  padding-left: 15px;
  //  margin-right: auto;
  //  margin-left: auto;
  //}
}

.container-fluid {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.bg {
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  background-image: url("./../img/main/desktop-bg.png");
  background-size: cover;
  
  @include lg-down {
    width: 100vw;
    min-height: 100vh;
    height: auto;
  }

  &__scroll-wrapper {
    display: flex;
    padding-bottom: 150px;
    position: relative;
    width: 100%;
    max-width: 1470px;
    margin-right: auto;
    margin-left: auto;
  
    @include xl-down {
      max-width: 1170px;
    }
  
    @include lg-down {
      max-width: 100%;
    }
  
    @include xl-down {
      //padding-top: 90px;
      padding-bottom: 115px;
    }
  }
}

