.logo {
    
    position: relative;
    width: 135px;
    height: 112px;
    padding: 22px 25px;
    border-radius: 0 0 19px 19px;
    background-color: #00a0e0;
    background-image: linear-gradient(45deg, #0392cb 0%, #00b3fb 100%);
    z-index: 2;
    
    &:before {
        content: '';
        position: absolute;
        bottom: -8px;
        left: 50%;
        transform: translateX(-50%);
        width: 117px;
        height: 111px;
        border-radius: 19px;
        background-color: #00a0e0;
        background-image: linear-gradient(45deg, #0392cb 0%, #00b3fb 100%);
        opacity: 0.7;
        z-index: -1;
    }
    
    &__title {
        color: #ffffff;
        font-family: "KOMIKA AXIS";
        font-size: 30px;
        font-weight: 400;
        line-height: 1;
        text-transform: uppercase;
        letter-spacing: 1.28px;
        margin-bottom: 10px;
        text-align: center;
    }
    
    &__undertitle {
        font-size: 16px;
        font-weight: 400;
        line-height: 24px;
        color: #ffffff;
        text-align: center;
    }
}