.test {
    
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    //height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(31, 31, 31, 0.70);
    z-index: 99;
    
    @include lg-down {
       align-items: flex-start;
        bottom: unset;
    }
    
    &__wrap {
    
    }
    
    &__form {
        padding-top: 15px;
        padding-bottom: 15px;
        
        @include lg-down {
        
        }
    }
    
    &__block {
        display: flex;
        width: 100%;
        min-height: 650px;
        
        @include md-down {
            flex-direction: column;
        }
        
        @include sm-down {
            //align-items: center;
            //justify-content: center;
            min-height: calc(100vh - 30px);
            height: 100%;
        }
    }
    
    &__block-left {
        max-width: 270px;
        width: 100%;
        padding: 60px 42px;
        border-radius: 30px 0 0 30px;
        background-color: #94e0ff;
        background-image: url("./../img/main/leftside-img.png"), linear-gradient(26deg, #0392cb 0%, #94e0ff 100%);
        background-repeat: no-repeat;
        background-size: 100%;
        background-position: 50% 101%;
        
        @include md-down {
            height: 100%;
            display: none;
        }
        
    }
    
    &__block-right {
        position: relative;
        width: 100%;
        padding: 60px 90px;
        background-color: #ffffff;
        border-radius: 0 30px 30px 0;
        
        @include lg-down {
            padding: 60px 70px;
        }
        
        @include md-down {
            border-radius: 30px;
        }
        
        @include sm-down {
            padding: 30px 20px;
        }
    }
    
    &__right-wrap {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    
    &__block-close {
        position: absolute;
        top: 30px;
        right: 30px;
        width: 30px;
        cursor: pointer;
        transition: all 0.2s ease;
        
        &:hover {
            opacity: 0.7;
        }
        
        img {
            display: block;
            width: 100%;
            height: auto;
        }
    }
    
    &__block-content {
    
    }
    
    &__left-text {
        color: #ffffff;
        max-width: 150px;
        font-size: 24px;
        margin-bottom: 30px;
    }
    
    &__sale {
        color: #ffffff;
        font-family: "Gilroy";
        font-size: 85px;
        font-weight: 700;
    }
    
    &__title {
        color: #151a20;
        font-size: 28px;
        font-weight: 300;
        margin-bottom: 30px;
        text-align: center;
        max-width: 550px;
        
        @include sm-down {
            padding-top: 40px;
        }
    }
    
    &__stepline {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 50px;
        
        @include sm-down {
            display: none;
        }
    }
    
    &__stepline-line {
        width: 95px;
        height: 6px;
        border-radius: 3px;
        background-color: #eff3f5;
        margin-right: 25px;
        background-image: url("./../img/main/rectangle-test.png");
        
        &:last-child {
            margin-right: 0;
        }
        
        &--active {
            background-color: #00a6e8;
            background-image: url("./../img/main/rectangle-test-active.png"), linear-gradient(to top, #0392cb 0%, #00b3fb 100%);
        }
    }
    
    &__step-title {
        color: #434947;
        font-size: 20px;
        font-weight: 700;
        text-align: center;
        margin-bottom: 14px;
    }
    
    &__step-undertitle {
        font-size: 24px;
        font-weight: 300;
        color: #1f1f22;
        text-align: center;
    }
    
    
    &__btn {
        padding: 24px 55px;
        font-size: 14px;
        font-weight: 700;
        line-height: 21px;
        letter-spacing: 1.8px;
        box-shadow:  0 0 39px 1px rgba(100, 177, 248, 0.46);
    
        @include sm-down {
            display: block;
            margin: 0 auto;
        }
    }
    
    
}

