.content-1-1 {
    display: flex;
    padding-top: 44px;
    padding-bottom: 32px;
    flex-grow: 1;
    height: 100%;
    
    @include sm-down {
        flex-direction: column;
    }
    
    
    &__wrap {
        margin-right: 80px;
        flex-grow: 1;
        height: 100%;
        
        @include sm-down {
            margin-right: 0;
        }
        
        &:last-child {
            margin-right: 0;
        }
    }
    
    &__checkbox {
        margin-bottom: 38px;
        max-width: 300px;
        label {
        
        }
        
        span {
            display: block;
            font-size: 18px;
            font-weight: 300;
            color: #393939;
            position: relative;
            padding: 0 0 0 60px;
            cursor: pointer;
            
            &:before {
                content: '';
                position: absolute;
                top: -2px;
                left: 0;
                width: 50px;
                height: 26px;
                border-radius: 13px;
                background: #CDD1DA;
                box-shadow: inset 0 2px 3px rgba(0,0,0,.2);
                transition: .2s;
            }
            
            &:after {
                content: '';
                position: absolute;
                top: 0px;
                left: 2px;
                width: 22px;
                height: 22px;
                border-radius: 10px;
                background: #FFF;
                box-shadow: 0 2px 5px rgba(0,0,0,.3);
                transition: .2s;
            }
        }
        
        input {
            position: absolute;
            z-index: -1;
            opacity: 0;
            margin: 10px 0 0 20px;
        }
    
        input:checked + span:before {
            background: #85d7f9;
        }
        input:checked + span:after {
            left: 26px;
        }
        input:focus + span:before {
            box-shadow: inset 0 2px 3px rgba(0,0,0,.2), 0 0 0 3px rgba(29, 29, 29, 0.06);
        }
    }
}

