.content-2 {
    padding: 40px 0;
    &__wrap {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 30px;
    
        @include sm-down {
            flex-direction: column;
            align-items: center;
            margin-bottom: 0;
        }
    }
    
    &__checkbox-img {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 160px;
        height: 105px;
        z-index: 1;
    }
    
    &__checkbox {
        position: relative;
        margin-right: 30px;
        
        @include sm-down {
            margin-right: 0;
            margin-bottom: 25px;
        }
        
        &:last-child {
            margin-right: 0;
        }
        
        label {
            display: block;
            position: relative;
            width: 160px;
            height: 105px;
            border-radius: 7px;
            background-image: linear-gradient(to bottom,  rgba(255,255,255,0.1) 0%, rgba(0,0,0,1) 130%);
            z-index: 2;
            cursor: pointer;
            
            input {
                position: absolute;
                z-index: -1;
                opacity: 0;
    
                &:checked ~ .content-2__checkbox-checked {
                    display: flex;
                   
                }
                
                
                &:checked + .content-2__checkbox-text {
                    display: none;
                }
                &:focus + span:before {
                    box-shadow: inset 0 2px 3px rgba(0,0,0,.2), 0 0 0 3px rgba(29, 29, 29, 0.06);
                }
            }
            
            .content-2__checkbox-checked {
                display: none;
                align-items: flex-end;
                justify-content: center;
                position: absolute;
                font-size: 14px;
                font-weight: 700;
                color: #ffffff;
                letter-spacing: 0.7px;
                text-transform: uppercase;
                text-align: center;
                top: 0;
                left: 0;
                width: 160px;
                height: 105px;
                z-index: 99;
                padding-bottom: 10px;
                border-radius: 7px;
                background-color: #22c9ed;
                background-image: linear-gradient(to top, #0392cb 0%, #00b3fb 100%);
                opacity: 0.9;
                
                &:after {
                    position: absolute;
                    top: 25px;
                    left: 50%;
                    transform: translateX(-50%);
                    content: url("./../img/main/block2/check.svg");
                }
            }
            
            .content-2__checkbox-text {
                position: absolute;
                bottom: 10px;
                left: 30px;
                display: block;
                font-size: 14px;
                font-weight: 700;
                color: #ffffff;
                letter-spacing: 0.7px;
                text-transform: uppercase;
                z-index: 3;
                
                &:before {
                    content: '';
                    width: 10px;
                    height: 10px;
                    position: absolute;
                    top: 50%;
                    left: -18px;
                    transform: translateY(-50%);
                    background-color: #FFFFFF;
                    border-radius: 50%;
                }
            }
    
           
        }
    }
    
    
}

