.content-3 {
    padding: 40px 0;
    
    &__wrap {
    
    }
    
    &__range-wrap {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        margin-bottom: 60px;
    }
    
    &__range-info {
        position: relative;
        text-align: left;
        font-size: 24px;
        font-weight: 300;
        background-color: #ffffff;
        padding: 10px;
        border-radius: 10px;
        
        &::after {
        
        }
        
        &--sm {
        
        }
    }
    
    &__range-slider {
        -webkit-appearance: none;  /* Override default CSS styles */
        appearance: none;
        width: 400px; /* Full-width */
        height: 5px; /* Specified height */
        background: #00a6e8; /* Grey background */
        outline: none; /* Remove outline */
        opacity: 0.7; /* Set transparency (for mouse-over effects on hover) */
        -webkit-transition: .2s; /* 0.2 seconds transition on hover */
        transition: opacity .2s;
        border-radius: 6px;
        
        @include sm-down {
            width: 100%;
        }
        
        &:hover {
            opacity: 1
        }
        
        &::-webkit-slider-thumb {
            position: relative;
            -webkit-appearance: none; /* Override default look */
            appearance: none;
            width: 20px; /* Set a specific slider handle width */
            height: 20px; /* Slider handle height */
            cursor: pointer; /* Cursor on hover */
            border-radius: 50%;
            box-shadow: -1px 0 14px 2px rgba(81, 81, 81, 0.22);
            border: 5px solid #ffffff;
            background-color: #00a6e8;
        }
        
        &::-moz-range-thumb {
            -webkit-appearance: none; /* Override default look */
            appearance: none;
            width: 20px; /* Set a specific slider handle width */
            height: 20px; /* Slider handle height */
            cursor: pointer; /* Cursor on hover */
            border-radius: 50%;
            box-shadow: -1px 0 14px 2px rgba(81, 81, 81, 0.22);
            border: 5px solid #ffffff;
            background-color: #00a6e8;
        }
        
        &::-ms-fill-upper {
            background-color: #FFFFFF;
        }
    }
    
    &__input {
        display: block;
        max-width: 500px;
        width: 100%;
        font-size: 123px;
        font-weight: 400;
        color: #535354;
        text-align: center;
        border-radius: 20px;
        outline: none;
        border: 1px solid #eee;
    }
    
}

