.btn {
    position: relative;
    display: inline-block;
    border: 0;
    border-radius: 29px;
    background-color: #64b1f8;
    background-image: linear-gradient(to top, #4388c1 5%, #0392cb 0%, #00b3fb 100%);
    color: #FFFFFF;
    font-size: 17px;
    font-weight: 700;
    line-height: 28.59px;
    letter-spacing: 1.6px;
    padding: 30px 68px;
    transition: all 0.2s ease;
    outline: none;
    text-transform: uppercase;
    z-index: 2;
    box-shadow: inset 0 -6px 0 #4388c1;
    
    @include sm-down {
        font-size: 14px;
        padding: 26px 38px;
    }
    
    &:hover {
        box-shadow: inset 0 -6px 0 transparent;
        background-image: linear-gradient(to top, #4388c1 5%, #0392cb 0%, #00b3fb 50%);
    }
    
    
    &--form {
        font-size: 14px;
        font-weight: 700;
        line-height: 21px;
        letter-spacing: 1.8px;
        padding: 23px 90px;
    }
}