.content {
    flex-grow: 1;
    //height: calc(100vh - 112px);
    
    .container {
        height: 100%;
    }
    
    &__wrap {
        height: 100%;
        display: flex;
        align-items: center;
        
        @include md-down {
            flex-direction: column;
        }
    }
    
    &__text {
        min-width: 50%;
        
        @include md-down {
            text-align: center;
            padding: 50px 0;
        }
    }
    
    &__title {
        color: #1f1f22;
        font-size: 68px;
        font-weight: 100;
        margin-bottom: 31px;
        line-height: 80px;
        
        @include lg-down {
            font-size: 60px;
            line-height: 72px;
        }
        
        @include md-down {
            font-size: 45px;
            line-height: 55px;
        }
        
        @include sm-down {
            font-size: 36px;
            line-height: 42px;
        }
        
        span {
            font-weight: 400;
        }
    }
    
    &__desc {
        font-size: 26px;
        font-weight: 300;
        line-height: 41px;
        margin-bottom: 58px;
        max-width: 500px;
        
        @include md-down {
            font-size: 20px;
            line-height: 30px;
            margin-bottom: 40px;
        }
        
        span {
            font-weight: 500;
        }
    }
    
    &__btn {
        //margin-bottom: 150px;
    }
    
    &__img {
        width: 100%;
        align-self: flex-end;
        position: relative;
        bottom: 0;
        z-index: 2;
        display: flex;
        align-items: center;
        
        @include sm-down {
            display: none;
        }
    }
    
    &__girl-img {
        position: relative;
        z-index: 2;
        img {
            position: relative;
            z-index: 2;
            width: 95%;
            height: auto;
            
            @include lg-down {
                width: 82%;
            }
        }
    }
    
    &__price {
    
    }
    
    &__price-circle {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        right: -10%;
        top: 47%;
        width: 217px;
        height: 217px;
        padding-bottom: 20px;
        padding-left: 20px;
        box-shadow: 8px 9px 30px 5px rgba(12, 12, 12, 0.07);
        border-radius: 50%;
        background-image: linear-gradient(60deg, #0392cb 0%, #55ceff 100%);
        z-index: 3;
        
        @include lg-down {
            right: 12%;
            top: 55%;
        }
        
        &:before {
            content: '';
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            width: 196px;
            height: 196px;
            border-radius: 50%;
            background-color: transparent;
            border: 1px solid #ffffff;
            opacity: 0.3;
            z-index: 4;
        }
        
        &:after {
            content: '';
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            width: 330px;
            height: 330px;
            background-image: url("./../img/main/stroke-outside.png");
            background-size: cover;
            z-index: 1;
        }
        
    }
    
    &__price-circle-text {
        //position: absolute;
        //width: 100%;
        //height: 100%;
        //left: 50%;
        //top: 50%;
        //transform: translate(-50%, -50%);
        color: #ffffff;
        transform: skew(0deg, -7deg);
        
        &-1 {
            font-size: 18px;
            line-height: 26px;
            font-weight: 400;
            padding-left: 2px;
            padding-bottom: 12px;
        }
    
        &-2 {
            font-size: 60px;
            line-height: 40px;
            font-weight: 700;
            span {
                font-size: 36px;
                font-weight: 700;
                padding-left: 5px;
                
                img {
                    display: inline-block;
                    width: 25px;
                    vertical-align: baseline;
                }
            }
        }
    
        &-3 {
            line-height: 30px;
            font-size: 30px;
            font-weight: 700;
            padding-left: 4px;
            
            sup {
                font-size: 16px;
            }
        }
    }
    
}