.popup {
    
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(31, 31, 31, 0.70);
    z-index: 99;
    
    @include xs-down {
    
    }
    
    &__wrap {
    
    }
    
    &__form {
        padding: 65px 90px 35px;
        position: relative;
        border-radius: 25px;
        background-color: #ffffff;
        box-shadow: inset 0px 0px 0px 7px #ffffff, inset 0px 0px 0px 8px rgba(194, 194, 194, 0.5);
        
       @include xs-down {
           padding: 50px;
       }
    }
    
    &__title {
        color: #1f1f22;
        font-size: 36px;
        font-weight: 300;
        margin-bottom: 18px;
        
        @include xs-down {
            text-align: center;
            font-size: 30px;
        }
    }
    
    &__undertitle {
        max-width: 252px;
        text-align: center;
        color: #1f1f22;
        font-size: 18px;
        line-height: 1.5;
        font-weight: 300;
        margin-bottom: 10px;
    }
    
   
    
    &__group-input {
    
    }
    
    &__input-wrap {
        max-width: 230px;
        width: 100%;
        margin: 0 auto;
        position: relative;
        transition: all 0.3s ease;
    
        &:hover,
        &:active {
            
            .popup__input {
                color: #00a8ec;
                border-bottom: 1px solid #00a8ec;
                transition: all 0.3s ease;
            }
            
            .popup__icon {
                color: #00a8ec;
                transition: all 0.3s ease;
            }
        }
    }
    
    &__icon {
        position: absolute;
        top: 46%;
        left: 0;
        transform: translateY(-50%);
        width: 16px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 auto;
        transition: all 0.3s ease;
    }
    
    &__input {
        max-width: 230px;
        width: 100%;
        border: 0;
        outline: none;
        padding: 16px 34px;
        border-bottom: 1px solid #c2c2c2;
        transition: all 0.3s ease;
        margin-bottom: 5px;
    }
    
    &__btn-wrap {
        
        @include xs-down {
            display: flex;
            justify-content: center;
            margin: 0 auto;
        }
    }
    
    &__btn {
        display: block;
        margin-top: 30px;
        margin-bottom: 33px;
        box-shadow: 5px 56px 46px rgba(12, 12, 12, 0.09), 0 0 39px 1px rgba(100, 177, 248, 0.46);
        
        @include xs-down {
        
        }
    }
    
    &__personal {
        margin: 0 auto;
        max-width: 220px;
        text-align: center;
        font-size: 14px;
        font-weight: 300;
        &-text {
        
        }
        
        &-link {
            color: #00a8ec;
            transition: all 0.3s ease;
            font-weight: 300;
            
            &:hover {
                text-decoration: underline;
            }
        }
    }
    
    &__close {
        margin-top: 30px;
        text-align: center;
        
        &-text {
            cursor: pointer;
            font-size: 16px;
            font-weight: 400;
            color: #939393;
            transition: all 0.3s ease;
            
            &:hover {
                text-decoration: underline;
                color: #7a7a7a;
            }
        }
    }
}