.header {
    
    
    &__wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    
    &__menu {
    
    }
    
    &__adress {
        @include md-down {
            display: none;
        }
    }
    
    &__logo {
        //margin-right: 186px;
        
        @include sm-down {
            margin: 0 auto;
        }
    }
    
    &__phone {
        display: flex;
        align-items: center;
        
        @include sm-down {
            display: none;
        }
    }
    
    &__phone-adds {
        display: none;
        text-align: right;
        
        @include md-down {
            display: block;
        }
    }
    
    &__wrap-phone {
        margin-right: 40px;
    }
    
    &__phone-number {
        display: block;
        color: #424752;
        font-size: 25px;
        font-weight: 300;
        line-height: 1;
        margin-bottom: 5px;
        transition: all 0.2s ease;
        
        &:hover {
            opacity: 0.7;
        }
    }
    
    &__phone-cons {
        color: #424752;
        font-size: 14px;
        font-weight: 300;
        //line-height: 24px;
        text-align: right;
        
        @include md-down {
            display: none;
        }
    }
    
    &__button {
        display: block;
        color: #424752;
        border-radius: 10px;
        border: 2px solid #00a0e0;
        padding: 18px 28px;
        font-size: 14px;
        font-weight: 700;
        line-height: 12.43px;
        text-transform: uppercase;
        letter-spacing: 1.96px;
        transition: all 0.2s ease;
    
        &:hover {
            //opacity: 0.7;
            background-color: #00a0e0;
            color: #ffffff;
        }
    }
    
   
}